import React, { Component } from 'react';
import Countdown from './Countdown.js';
import logo from './logo.svg';
import './App.css';


class ConferenceInfo extends Component {
  render() {
    return (
      <div className="confernenceInfo">
        <h1>{this.props.meta.conference}</h1>
        <h2>{this.props.meta.start_date}-{this.props.meta.end_date}</h2>
        <h2>{this.props.meta.location}</h2>
        <Countdown date={new Date(this.props.meta.paper_deadline)} />
      </div>
    );
  }
}

class App extends Component {
  constructor(){
    super() 
      this.state = {
        data: [{
          "conference": "CHI",
          'fullname': "The ACM Conference on Human Factors in Computing Systems ",
          "start_date": "04/25/2020",
          "end_date":"04/30/2020",
          'location':"Honolulu, HI, USA",
          'abstract_deadline':"09/13/2019 20:00 GMT",
          'paper_deadline':"09/19/2019 20:00 GMT",
          'url':"https://chi2020.acm.org/",
        },
        {
          "conference": "IUI",
          'fullname': "The ACM Conference on Human Factors in Computing Systems ",
          "start_date": "04/25/2020",
          "end_date":"04/30/2020",
          'location':"Honolulu, HI, USA",
          'abstract_deadline':"09/13/2019 20:00 GMT",
          'paper_deadline':"09/20/2019 20:00 GMT",
          'url':"https://chi2020.acm.org/",
        }]
      }
    
  };
  
  render() {
    return (
      <div className="App">
        <div className="App-header">
          {/* <img src={logo} className="App-logo" alt="logo" /> */}
          <h1>HCI Conference Deadlines</h1>
          <h2>Now is {new Date().toLocaleDateString()}.</h2>
        </div>
        <p className="App-intro">
          To get started, edit <code>src/App.js</code> and save to reload.
        </p>
            <div>{this.state.data.map(function(item, key) {
                     return (
                        <div key = {key}>
                          <ConferenceInfo meta={item} />
                        </div>
                      ) 
                   })}</div>
        </div>
    );
  }
}

export default App;



